.header {
	background-image: 'static/img/bannerart.jpg';
	background-repeat: no-repeat;
	background-attachment: fixed;
	background-size: cover;
	width: 100%;
	height: 25vh;
	margin:0;
	position:relative;
}
.title {
	font-family: Arial;
	font-size: 50pt;
	font-weight: bold;
	color: white;
	margin: 0;
	padding: 0;
	position: absolute;
	left: calc(50% - 150px);
	bottom: 0;
}

.appbar {
	background-color: rgba(145, 214, 148, .5) !important;
	width:30%;
}
.menutabs {
	color:#22545b;
	background-color:transparent;
}
.artcard {
	width:23%;
	display: inline-block;
	margin: 1%;
	position:relative;
	height: 50vh;
}
.artcardpiccontainer {
	height:40vh;
	background-color:lightgray;
}
.artcardpic {
	width:auto !important;
	position:absolute;
	left:50%;
	top:calc(50% - 5vh);
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	padding:0;
	max-height: 40vh;
	max-width: 100%;
}
